/* Important styles */
#toggle[data-v-7a843dae] {
  display: block;
  width: 28px;
  height: 30px;
  position: absolute;
  right: 25px;
  top: 40px;
  cursor: pointer;
}
#toggle span[data-v-7a843dae]:after,
#toggle span[data-v-7a843dae]:before {
  content: "";
  position: absolute;
  left: 0;
