#toggle span[data-v-7a843dae]:after{
  top: 9px;
}
#toggle span[data-v-7a843dae] {
  position: relative;
  display: block;
}
#toggle span[data-v-7a843dae],
#toggle span[data-v-7a843dae]:after,
#toggle span[data-v-7a843dae]:before {
  width: 100%;
  height: 5px;
  background-color: #888;
  -webkit-transition: all 1s;
  transition: all 1s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border-radius: 2px;
}

/* on activation */
#toggle.on span[data-v-7a843dae] {
  background-color: transparent;
}
#toggle.on span[data-v-7a843dae]:before {
  -webkit-transform: rotate(45deg) translate(5px, 5px);
          transform: rotate(45deg) translate(5px, 5px);
}
#toggle.on span[data-v-7a843dae]:after {
  -webkit-transform: rotate(-45deg) translate(7px, -8px);
          transform: rotate(-45deg) translate(7px, -8px);
}
#toggle.on + #menu[data-v-7a843dae] {
  opacity: 1;
  visibility: visible;
}
