
.sos-request{
  top: 25px;
  margin: 5px 5px 5px 5px;
}
.help-title{
  margin-bottom: 5px;
}
.call-help-btn{
  border-radius: 150px;
  height: 50px;
  width: 50px;
  line-height: 40px;
}
.record-help{
  margin-top: 40px;
}
.record-help-btn{
  height: 100px;
  width: 100px;
  line-height: 100px;
  border-radius: 100px;
  font-size: 40px;
}
.images-container{
  max-height: 85px;
  overflow: auto
}
div.user-image{
  width: 70px;
  float: left;
  margin: 5px;
}
div.user-image img{
  height: 70px;
  width: 70px;
  border-radius: 60px;
}
div.user-image .close{
  position: relative;
  top: -73px;
  left: 5px;
  font-size: 15px;
}
select.form-control.selectpicker.shown{
  display: block !important;
}



